  import { Card, CardBody } from "react-bootstrap";
  import { hideLangFilter, kFormatter } from "../../../../../utils";
  import React, { useEffect, useState } from "react";
  import AssetsbySkeleton from "../../layout/skeleton/assetsbySkeleton";
  import NoData from "../../layout/common/noData";
  import FilterModeration from "../../layout/common/filterModeration";
  import SectionHeading from "../../layout/common/sectionHeading";

  const TopAiTags = (props) => {

    const { topaiTags, top_ai_loader, selectedWallFeed } =props
    const [gettagList ,setgettagList] = useState([])
 
      const tagUpdate=()=>{

          const feedList = selectedWallFeed && Object.keys(selectedWallFeed).length > 0 ? selectedWallFeed.map(item => item.id) : [];

          const tagList = selectedWallFeed && Object.keys(topaiTags).length > 0 ? topaiTags.map(item => item.feedId) : [];
          const tagListData = [...new Map(tagList.map(item => [item, item])).values()].join(", ");

          let getTags = topaiTags;
              if(getTags && getTags.length > 0){
                getTags = getTags.filter((lang) => {
                  if(feedList.length){
                    if(hideLangFilter(feedList, tagListData,1)) return lang;
                      }else{
                   return lang;
                 }
              });

              getTags.splice(6)
              setgettagList(getTags)
          }
     }
 

  useEffect(() => {
      topaiTags && selectedWallFeed && Object.keys(topaiTags).length && tagUpdate()
  }, []);
    

    return (
      <Card className="sw-card-custom gutter-b mb-5 card-shadow">
        <SectionHeading icon={'ai'} section={2}  title={'Top AI Tags'} />
        <CardBody className="p-5">

         { top_ai_loader ? <AssetsbySkeleton/> : gettagList && Object.keys(gettagList).length ?
           gettagList.map((data,index)=>{
            return(
            <FilterModeration selectedWallFeed={selectedWallFeed} link={`tags=${data.tag}`}>
              <div className="px-5 py-2 border rounded">
                <div className="d-flex align-items-center justify-content-between">
                  <div className="d-flex align-items-center me-10">
                    <div className="text-dark font-weight-boldest me-2">{index+1}.</div>
                    <div className="">
                      <p className="text-dark mb-0 font-weight-boldest overflow-hidden text-nowrap text-ellipsis author_name text-capitalize">
                        {data.tag}
                      </p>
                    </div>
                  </div>
                  <p className="count-tag border font-weight-boldest m-0 text-dark">{kFormatter(data.count)}</p>
                </div>
              </div>
            </FilterModeration> 
            )
        }) :         
            <NoData/>
        }
        </CardBody>
      </Card>
    );
  };

 export default React.memo(TopAiTags);
